/* Flexbox
---------------------------------------------------------------------------------------------------- */
 
    .flex-container {
	
	display:flex;
	justify-content: space-between;
        align-items: center;
        
        }

    .box {
	padding: 0em;
	box-sizing: border-box;
	margin: 10px 0 0 0;

        }

    @media (min-width:$query__small) {

        .f-one {
	flex: 1;
        
        }
        
         .f-two {
	flex-basis: 49.7%;
        
        }
    }




.widget-area {
	margin-top: 0em;
}

.widget {
    
	margin: 0 0 2em;
	padding: 0 0 2em;
	font-family: $font__sans;
        
        h2 {
            font-size:1.2em;
        }

	ul,
	ol {
		padding: .7em 0 0;
		margin-bottom: 0;
		list-style-type: none;

		li {
			display: block;
			line-height: 2em;

			ul,
			ol {
				margin-left: .5em;

				@media screen and (min-width: 30em) {
					margin-left: 1em;
				}
			}

		}
	}

	a {
		font-weight: 400;
		text-decoration: none;

		&:hover,
		&:focus {
			border-bottom: 1px solid $color__border-button;
		}
	}

	/* Make sure select elements fit in widgets. */
	select {
		width: 100%;
		height: 2.5em;
		padding: 10px;
		font-family: $font__sans;
		border: 1px solid $color__border-button;
	}

}

.widget-title {
	margin-top: 0;
	margin-bottom: .5em;
	font-size: 1.1em;
}

/* Archive and Categories widgets */
.widget_archive li {
	color: $color__link;
}

.widget_categories .children {
	padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
	display: none;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
	padding-top: 0;
	padding-bottom: .5em;
}

.widget_pages,
.widget_nav_menu {

	a {
		display: block;
		padding: 1em 0;
		text-decoration: none;
		border-bottom: 1px dashed hsl(0, 0%, 50%);

		&:hover,
		&:focus {
			text-decoration: none;
			border-bottom: 1px dashed hsl(0, 0%, 50%);
		}
	}

	li {
		padding: 0;

	}

}


/* Meta widget */
.widget_meta li {
	padding: 0;
}

/* Recent comments */
.widget_recent_comments {

	li {
		line-height: 1.3em;
		padding-bottom: .5em;
	}

	a:hover,
	a:focus {
		padding-left: 0;
	}

}

/* RSS widget */
.widget_rss li {
	margin-bottom: 1em;
}
.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	font-family: $font__sans;
	font-size: .8em;
    line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid $color__border-form-field;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;

	caption {
		padding: .5em 0;
		font-size: 1em;
		font-weight: 500;
		text-align: left;
	}

	thead {
		background: transparent;
		color: black;

		th {
			border-bottom-width: 2px;
		}
	}

	td {
		padding: .2em;
		font-size: .8em;
		text-align: center;
		background: white;
		border: 1px solid hsl(0, 0%, 80%);
	}

	th {
		border: none;
		text-align: center;
	}

	.pad {
		background: #eaeaea;
	}

	a {
		font-weight: 900;
		text-decoration: none;
	}

}

/* Text widget */
.textwidget {
	font-size: 80%;
	line-height: 1.3em;
}


/* Footer widget stylings */

.footer-email {
        margin:0em;
	padding: 1em 0em;
	background-color: $color__footer-widget;
        
        @media screen and (min-width: $query__small) {
		padding: 1em 7em;
	
        
        
        }
        
        h2.widget-title {
                    font-size:1.9em;
                    text-align: center;
                }
                
                .textwidget {
                    font-size:1.1em;
                    text-align: center;
                }
        
        .widget {
		flex: 1;
		margin: 0 1em 0em;
		

		.original-title {
			color: $color__footer-widget-text;
		}
	}

	.widget,
	.widget-title,
	.widget a,
	.widget_calendar thead,
	.rss-date,
	.widget_rss cite {
		color: $color__footer-widget-text;
            
	}
}

.footer-widgets {
	margin: 0em;
	padding: 1em 0;
	background-color: $color__footer-widget;
   

	@media screen and (min-width: $query__small) {
		padding: 2em 1em;
	}

	.widget {
		flex: 1;
		margin: 0 1em 2em;
		padding-top: 1em;

		.original-title {
			color: $color__footer-widget-text;
		}
	}

	.widget,
	.widget-title,
	.widget a,
	.widget_calendar thead,
	.rss-date,
	.widget_rss cite {
		color: $color__footer-widget-text;
            
	}

	.widget_calendar td,
	.widget_calendar a {
		color: $color__footer-widget-text;
	}

	@media screen and (min-width: $query__medium) {
		/* Use flex to create a responsive layout */
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;

		/* If only one widget is displayed, make it full width. */
		.widget:only-of-type {
			width: 100%;
		}

		/* If 2 or 3 widgets are displayed, make the second one twice as wide. */
		.widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) {
			flex: 2;
		}

		/* If 4 or more widgets are displayed, show three across when available. */
		.widget:nth-last-child(n+4),
		.widget:nth-last-child(n+4) ~ .widget {
			width: 30%;
			flex: 1 1 30%;
		}
	}

}



